<template>

  <div class="container-fluid">
    <main  class="tm-main">
      <div class="row">
        <div class="col-md-3 col-sm-6" v-for="product in Categorys" :key="product.id">
          <div class="product-grid">
            <div class="product-image">
              <a @click="toMenu(product.id)" class="image">
                <img class="pic-1" :src="product.image">
                <img class="pic-2" :src="product.image">
              </a>
            </div>
            <div class="product-content">
              <h3 class="title"><a @click="toMenu(product.id)">{{product.category}}</a></h3>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Category",
  data(){
    return{
      Categorys:[],

    }
  },
  methods:{
    async LoadCategory(){
      this.Categorys = await axios({
        method: 'get',
        url: 'https://smart-shelf-bbc6g.ondigitalocean.app/api/v1/category/',
        headers: {
          Authorization: 'Token ' + localStorage.getItem("auth_token")
        }
      }).then(response => response.data);

    },
    toMenu(category){
      this.$router.push({name:'menu', params:{category:category}})
    }
  },
  created() {
    this.LoadCategory()
  }
}
</script>

